import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Appindex from '@/components/home/Appindex'

Vue.use(Router)

export default new Router({
  mode: 'history', // 默认是Hash模式 修改为history模式
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/index',
      name: 'Appindex',
      component: Appindex
    }
  ]
})



