<template>
  <el-form class="login-container">
    <h3 class = "login-title">登录界面</h3>
    <el-form-item>
      <el-input v-model="loginForm.username" placeholder="请输入用户名"/>
    </el-form-item>
    <el-form-item>
      <el-input v-model="loginForm.password" type="password" placeholder="请输入密码"/>
    </el-form-item>
    <el-form-item style="width:100%">
      <el-button type="primary" style="width: 100%;background: #505458;border: none" @click="login" v-on:keyup.enter="login">登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      reponseResult: []
    }
  },
  methods: {
    login () {
      this.$axios
        .post('/login', {
          username: this.loginForm.username,
          password: this.loginForm.password
        }).then(successRes => {
          if (successRes.data.code === 200) {
            this.$router.replace('/index')
          } else {
            alert('账号密码有误')
          }
        })
        .catch(failResponse => {
        })
    }
  }
}
</script>

<style scoped>
  .login-container{
    border-radius: 15px;
    border:1px solid #eaeaea;
    width:350px;
    padding: 15px 15px 15px 15px;
    margin: 0 auto;
    box-shadow: 0 0 25px #cac6c6;
  }
</style>



